<template>
  <div class="teacher-class-mana">
    <router-view></router-view>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  }
};
</script>
<style lang="less">
.teacher-class-mana {
  width: 925px;
  margin: 0 auto;
  .teacher-class-header {
    width: 100%;
    height: 40px;
    background: #fff;
    padding: 7px 22px;
    .title {
      float: left;
      margin-top: 2px;
    }
    .iconfont {
      float: right;
      margin-left: 18px;
      padding: 2px 5px;
      border-radius: 3px;
      &:hover {
        cursor: pointer;
      }
    }
    .choosed {
      background: #3fabff;
      color: #fff;
    }
  }
  .teacher-class-sub-panel {
    width: 100%;
    margin-top: 22px;
    background: #fff;
    padding: 12px 22px;
    .head {
      width: 100%;
      height: 28px;
      .data {
        float: left;
        padding: 1px 3px 3px;
        border-radius: 3px;
        margin-right: 8px;
        &:hover {
          cursor: pointer;
        }
      }
      .choosed {
        background: #3fabff;
        color: #fff;
      }
      .choose-data {
        float: left;
        margin-left: 12px;
        .el-input__icon {
          line-height: 25px !important;
        }
        .el-input__inner {
          height: 25px !important;
          line-height: 25px !important;
          border: none !important;
        }
      }
      .icon {
        float: right;
        margin-left: 22px;
        .text {
          font-size: 0.9rem;
        }
        .iconfont {
          vertical-align: middle;
        }
      }
    }
    .chart-box {
      width: 100%;
      height: 360px;
    }
  }
}
</style>



